import React, { ReactNode } from "react";

import cx from "classnames";

import styles from "./style.module.css";

type Props = {
  children: ReactNode;
  pageCenter?: boolean;
  fullWidth?: boolean;
  background?: "light";
};

const Container: React.FC<Props> = ({
  children,
  pageCenter,
  fullWidth,
  background,
}) => {
  const bg = background && styles[background];
  return (
    <div
      className={cx(
        styles.container,
        bg,
        { [styles.pageCenter]: pageCenter },
        { [styles.fullWidth]: fullWidth }
      )}
    >
      {children}
    </div>
  );
};

export default Container;
